.header {
    height: 100px;
    background-color: white;
}

.header-wrapper {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: space-between;
}

.nav {
    display: flex;
    width: 370px;
    justify-content: space-between;
}

.nav-item.active a, .nav-item:hover a {
    color: #C9A96E;
}

@media (max-width: 730px) {
    .nav {
      display: none;
    }
    .header-wrapper {
        justify-content: center;
    }
  }